import React from 'react';
import Home from './Componentes/home';
import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'


function App() {


    return (
    <div className="App">
     <Router basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route path="/" component={Home} exact />
        </Switch>        
      </Router>
    </div>
  );
}


export default App;
